@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sen:wght@400;700;800&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Sen", sans-serif;
  font-family: var(--Sen);
}

:root {
  --MainColor: #fff;
  --lightSmook: #efefef;
  --lightRed: rgb(206, 17, 17);
  --Righteous: "Righteous", cursive;
  --Sen: "Sen", sans-serif;
}


.CustomButton {
  border: none;
  padding: 1rem 2.5rem;
  background-color: var(--lightRed);
  color: var(--MainColor);
  cursor: pointer;
  border-radius: 5px;
}

.card {
  width: 350px;
  height: 480px;
  margin: 2rem 0.5rem;
  background-size: cover;
  background-position: center;
  border-radius: 5px;
  transition: all 0.3s ease;
}
.card:hover {
  -webkit-transform: translateY(-8px);
          transform: translateY(-8px);
}

.TvShowHeader {
  width: 100%;
  display: flex;
  border-bottom: 1px solid #efefef;
  margin-top: 1.5rem;
}
.TvShowButton {
  padding: 1rem 4rem;
  background-color: red;
  -webkit-clip-path: polygon(7% 0, 100% 0%, 93% 100%, 0% 100%);
          clip-path: polygon(7% 0, 100% 0%, 93% 100%, 0% 100%);
}
.TvShowButton p {
  color: var(--MainColor);
  font-size: 1.2rem;
}

.ContenueWatchingInnerCard {
  width: 300px;
  height: auto;
  padding: 0.6rem 0.6rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ContenueWatchingImgDiv {
  width: 120px;
  height: 120px;
  background-position: center;
  background-size: cover;
}
.ContenueWatchingContentDiv {
  color: var(--MainColor);
  max-width: 200px;
  margin: 0 1rem;
  flex: 1 1;
}
.ContenueWatchingContentDiv h3 {
  font-size: 1.2rem;
}
.ContenueWatchingContentDiv p {
  font-size: 0.5rem;
  margin: 0.5rem 0;
}
.average {
  width: 100%;
  height: 0.2vh;
  position: relative;
  background-color: var(--MainColor);
}
.average::before {
  content: "";
  width: 80%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: red;
}

.TvShowDiv {
  width: 100%;
  height: auto;
  padding: 40px 0;
  background-color: #000;
  background-position: center;
  background-size: cover;
}
.Container {
  max-width: 1600px;
  margin: auto;
}
.TvShowInner {
  max-width: 1600px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}
.ContenueWatchingDivCard {
  width: 100%;
  height: auto;
  padding: 20px 0;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  background-color: #000;
}

.BannerComponentDiv {
  width: 100%;
  height: 100vh;
  background-image: url("/react-movies-app/static/media/Group 36.6253978c.png");
  background-position: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.BannerComponentDiv iframe {
  position: absolute;
  top: 0;
  left: 0;
  min-width: 100%;
  min-height: 100%;
  display: none;
}
.ShowVedio {
  display: block !important;
}

.BannerComponentDiv > div {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.showBannerSection {
  display: none;
}

.BannerIconDiv {
  width: 60px;
  height: 60px;
  border: 2px solid var(--MainColor);
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
}

.BannerIconDiv i {
  color: var(--MainColor);
  cursor: pointer;
}

.Line {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  border: 3px solid rgb(255, 145, 0);
  display: flex;
  justify-content: center;
  align-items: center;
}

.Line h1 {
  color: rgb(255, 145, 0);
  font-size: 3rem;
}
.rangeDiv > div {
  margin: 1rem 1rem;
}
.range {
  width: 200px;
  height: 0.5vh;
  background-color: var(--MainColor);
  position: relative;
}
.range::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 80%;
  height: 100%;
  background-color: rgb(255, 145, 0);
}

.bannerSecondContent {
  margin: 0 1rem;
  max-width: 600px;
}

.bannerSecondContent h1 {
  color: var(--MainColor);
  font-size: 3rem;
}

.bannerSecondContent p {
  color: #999;
  margin: 1rem 0;
  letter-spacing: 1.3px;
}

.MemberShipBanner {
  width: 100%;
  padding: 40px 0;
  height: auto;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.MemberShipBanner h3 {
  color: var(--lightSmook);
  font-size: 1.4rem;
  font-weight: 400;
  margin: 1rem 0;
}
.MemberShipBanner .MemberShipInnerDiv {
  text-align: center;
}

.MemberShipInnerDiv input {
  width: 650px;
  height: 60px;
  margin: 1rem 0;
  padding: 1rem;
  outline: none;
  border-radius: 10px 0px 0px 10px;
  outline: none;
}
.hideInput {
  display: none;
}
.GetStart {
  padding: 1.4rem 1.5rem;
  border: none;
  background-color: transparent;
  outline: none;
  background-color: var(--lightRed);
  color: #fff;
  font-weight: 600;
  cursor: pointer;
}

.HomePageDiv {
  width: 100%;
  height: 100vh;
  background-image: url(/react-movies-app/static/media/bg.49cfbf1d.png);
  background-position: center;
  background-size: cover;
}

.HomePageContent {
  text-align: center;
  padding: 30px 0;
}
.HomePageContent h1 {
  font-size: 3.5rem;
  max-width: 650px;
  margin: auto;
  text-align: left;
  color: var(--MainColor);
  font-family: var(--Righteous);
}
.spacer {
  width: 100%;
  height: 50%;
}
.buttonDiv {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
}

.Navbar {
  width: 100%;
  height: auto;
  padding: 30px 0;
  background: #000;
  position: relative;
}
.Navbar-Containr {
  max-width: 1600px;
  margin: auto;
}
.Innner-Navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Innner-Navbar h1 {
  color: var(--MainColor);
  font-size: 2.2rem;
}
.Innner-Navbar ul li {
  display: inline;
  margin: 0 2rem;
  color: var(--lightSmook);
  cursor: pointer;
  font-size: 1.1rem;
}
.active {
  position: fixed;
  background-color: transparent;
  z-index: 999;
}

.SideBar {
  width: 100px;
  height: 100vh;
  position: absolute;
  top: 0;
  right: 0;
  background-color: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
}

.iconDiv i {
  color: var(--MainColor);
}
.iconInner {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid var(--MainColor);
  border-radius: 50%;
  margin: 1rem 0;
  transition: all 0.3s ease;
}
.iconInner:hover {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}

.vedioPlayDiv {
  width: 95%;
  height: 90vh;
  position: relative;
}
.youtubeVedio {
  min-width: 100%;
  min-height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}


.FooterDiv {
  width: 100%;
  height: auto;
  padding: 40px 0;
  background-color: #000;
}
.Links {
  max-width: 1400px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.Links ul li {
  list-style: none;
}
.Links ul .LinkTitle {
  color: var(--MainColor);
  margin: 1rem 0;
}
.Links ul li {
  color: #efefef;
  font-size: 0.8rem;
  margin: 0.5rem 0;
}

